const Sample = (props) => {
  return {
    styles: `.symplor_sample {background: ${props.config.bgColor}}`,
    html: `<div class="symplor_sample">Sample Component ${props.data.text}</div>`,
    onLoad: (el) => {
      el.addEventListener('click', (e) => {
        console.log('Sample Component Clicked')
      })
    },
  }
}
window.symplor = window.symplor || { components: {} }
window.symplor.components.sample = Sample
